import React, { Component } from 'react';
import './Project.css';

import {
  Card, CardText, CardBody,
  CardTitle, CardSubtitle
} from 'reactstrap';

export default class Project extends Component {
  render() {
    return (
      <div className="Project">
        <Card inverse style={{ backgroundColor: '#333', borderColor: '#333', width: '100%' }}>
          <CardBody>
            <a class="card-link" href={this.props.data.link} ></a>
            <CardTitle>{this.props.data.name}</CardTitle>
            <CardSubtitle style={{fontSize: '12px'}}>{this.props.data.tools}</CardSubtitle>
            <CardText style={{fontSize: '16px'}}>{this.props.data.desc}</CardText>
          </CardBody>
        </Card>
      </div>
    );
  }
}
